export default allEntries => {
  let allDisciplines = [];
  let allCategories = [];
  let tags = {};

  // for now tags are fixed to these disciplines
  const fixedDisciplines = ['brand & identity', 'film & photography', 'spatial', 'digital'];

  for (const { disciplines, categories } of allEntries) {
    for (const { title } of disciplines) {
      let discipline = title.toLowerCase();
      allDisciplines.push(discipline);
    }

    for (const { title } of categories) {
      let category = title.toLowerCase();
      allCategories.push(category);
    }
  }

  const disciplines = [...new Set(allDisciplines)];
  // const categories = [...new Set(allCategories)];

  // ensure tags is actually being used from fixed tags
  tags.disciplines = fixedDisciplines.filter(discipline => disciplines.includes(discipline));

  // tags = { disciplines, categories };
  return tags;
};
