import React from 'react';
import { graphql } from 'gatsby';

import Layout from '../components/common/layout';
import CreateTiles from '../components/common/createTiles';

import WorkNav from '../components/work/workNav';
import filterArticles from '../components/work/filterArticles';
import findTags from '../components/work/findTags';

export default ({ data, pageContext }) => {
  const allEntries = data.craft.entries;
  const { discipline } = pageContext;
  const tags = findTags(allEntries);
  const tiles = filterArticles(allEntries, discipline);
  const seomatic = data.craft.seomatic;

  return (
    <Layout className="work" seo={seomatic}>
      {tags && <WorkNav data={tags} />}
      {tiles && <CreateTiles data={tiles} />}
    </Layout>
  );
};

export const query = graphql`
  query {
    craft {
      seomatic(uri: "/work") {
        metaTitleContainer
        metaTagContainer
        metaLinkContainer
        metaScriptContainer
        metaJsonLdContainer
      }
      entries(section: [projects], hidden: false) {
        ... on Craft_Projects {
          hidden
          slug
          title
          previewTile {
            id
            dateUpdated
            url

            ... on Craft_UploadsVolume {
              __typename
              imageFile {
                childImageSharp {
                  fluid(maxWidth: 650) {
                    src
                    sizes
                    srcSet
                    srcWebp
                    srcSetWebp
                    aspectRatio
                  }
                }

                colors {
                  muted
                }
              }
            }
          }
          description {
            content
          }
          categories {
            title
          }
          disciplines {
            title
          }
        }
      }
    }
  }
`;
